.loginContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.fakeTable {
    filter: blur(1em);
    width: 100%;
    height: 100%;
    z-index: -1;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

input, textarea {
    margin: 0;
    max-width: 100%;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    outline: 0;
    -webkit-tap-highlight-color: rgba(255,255,255,0);
    text-align: left;
    line-height: 1.21428571em;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    padding: .67857143em 1em;
    background: #fff;
    border: 1px solid rgba(34,36,38,.15);
    color: rgba(0,0,0,.87);
    border-radius: .28571429rem;
}

button {
    background-color: #202020;
    cursor: pointer;
    display: inline-block;
    min-height: 1em;
    outline: 0;
    border: none;
    vertical-align: baseline;
    color: white;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    padding: .78571429em 1.5em .78571429em;
    text-transform: none;
    text-shadow: none;
    font-weight: 300;
    font-style: normal;
    text-align: center;
    text-decoration: none;
    border-radius: .28571429rem;
    box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgb(34 36 38 / 15%) inset;
    letter-spacing: 0.1em;
}

.login button {
    margin: 0 .25em 0 0;
}

.connecting {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
