@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');

body {
    font-family: 'Roboto', sans-serif;
}

.layout {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.contactUs {
    cursor: pointer;
    margin-top: 5em;
}

.footer {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-grow: 1;
}

.contactUsWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}
