.contact {
    position: fixed;
    transition: transform 1s;
    background: white;
    bottom: -20em;
    display: flex;
    width: 100%;
    flex-direction: column;
    z-index: 1;
    justify-content: center;
    align-items: center;
    padding-top: 1em;
}

.contact input, textarea {
    margin-bottom: 1em;
}

.contact button {
    margin-top: 0.5em;
}

textarea {
    height: 10em;
}

.visible {
    bottom: 0;
}

.send {
    background-color: #3496E0;
}

.center {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 30em;
}
