.room {
    width: 100%;
    height: 100%;
}

.header {
    box-shadow: 0px 4px 10px 0px #0000000D;
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    position:absolute;
    width: 100%;
    transition: transform 0.5s;
    transform: translateY(-3em);
    background: white;
}

.header.loaded {
    transform: translateY(0);
}

.header button {
    height: 3em;
}

.container {
    height: 100%;
}

.tables {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.add {
    background-color: #3496E0;
}

.add:disabled {
    cursor: not-allowed;
}

.invite {
    margin-left: 1em;
}

.inviteBar {
    transition: transform 0.2s;
    position: absolute;
    transform: translateY(-2em);
    width: 100%;
    text-align: center;
}

.inviteBar.show {
    transform: translateY(4em);
}
