.table {
    width: 70%;
    height: 20em;
    position: relative;
    background-color: #EEF2F8;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 5em 0;
    border-radius: 10em;
    user-select: none;
}

.seat {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}

.stats {
    position: absolute;
    margin-top: -7em;
}

.actions {
    text-align: center;
}

.actions button {
    margin: 0.5em;
    background-color: #3496E0;
}

.name {
    transform: translateY(2em) translateX(-50%);
    position: absolute;
    font-size: 1em;
    color: #424D61;
    text-transform: uppercase;
    width: 12em;
    text-align: center;
}

.card {
    background: #DCE1EA;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 0;
    flex-shrink: 0;
    font-size: 1em;
    border-radius: 0.2em;
    width: 2.1em;
    height: 3em;
    margin-top: 1em;
    margin-right: 0.3em;
    transition: transform 0.5s;
}

.card button {
    color: #424D61;
    font-weight: 500;
}

.card button:disabled {
    cursor: not-allowed;
}

.cards {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: -2.5em;
    left: 0;
    transform: translateX(-50%);
}

.selected {
    background-color: #202020;
}

.selected,
.selected button {
    color: white;
}

.me .selected {
    transform: translateY(-0.5em);
}

.seat button {
    padding: 0;
}

.card button {
    border: none;
    width: 100%;
    height: 100%;
    background: transparent;
    font: inherit;
    cursor: pointer;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    font-size: 0.8em;
}

.otherPlayerSelected {
    transform: translateY(-5em) translateX(-50%);
    position: absolute;
}

.playerCard {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 0;
    flex-shrink: 0;
    font-size: 1em;
    border-radius: 0.2em;
    width: 2.1em;
    height: 3em;
    margin-top: 1em;
    margin-right: 0.3em;
    transition: transform 0.5s;
    -webkit-perspective: 800;
}
.playerCard .faces.flipped {
    -webkit-transform: rotatey(-180deg);
}
.playerCard .faces {
    -webkit-transform-style: preserve-3d;
    -webkit-transition: 0.5s;
    width: 100%;
    height: 100%;
}
.playerCard .faces .face {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
}

.playerCard .selected,
.playerCard .resultCard {
    width: 100%;
    height: 100%;
    border-radius: 0.2em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.playerCard .selected {
    background-color: #202020;
    color: white;
}

.resultCard {
    background: #DCE1EA;
    color: #424D61;
}

.playerCard .front {
    position: absolute;
    width: 100%;
    height: 100%;
}

.playerCard .back {
    -webkit-transform: rotatey(-180deg);
    width: 100%;
    height: 100%;
}

.visible {
    opacity: 1;
}

.hidden {
    opacity: 0;
}
